<template>
  <v-app :style="appStyle">

    <v-dialog v-model="done" max-width="750">
      <v-card class="mx-auto" max-width="750" outlined>
        <v-card-title
          class="text-h7 white--text"
          style="font-weight:700; background-color: #0f0f33;"
          >{{translate('Libyano Robot')}}</v-card-title
        >
        <v-card-text
          class="text--primary"
          style="padding-top:60px; margin-bottom:20px; font-size:18px; text-align:center;"
        >
          {{
            translate(
              "Thank you for using Libyano Robot! Its been my pleasure to serve you!"
            )
          }}
        </v-card-text>
        <v-card-text
          class="text--primary"
          style="padding-top:10px; margin-bottom:20px; font-size:18px; text-align:center;"
        >
          {{
            translate(
              "If you're done using Libyano, please tap on the 'Yes' button. If you would still like to explore Libyano, Tap on the 'No' button."
            )
          }}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions class="d-flex justify-center">
          <v-btn color="error" large width="160" @click="rateWindow()">
            {{translate('Yes')}}
          </v-btn>
          <v-btn color="primary" large width="160" @click="done = false">
            {{translate('No')}}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="rate" max-width="750">
      <v-card class="mx-auto" max-width="750" outlined>
        <v-card-title
          class="text-h7 white--text"
          style="font-weight:700; background-color: #0f0f33;"
          >{{translate("Libyano Robot")}}</v-card-title
        >
        <v-card-text
          class="text--primary"
          style="padding-top:60px; margin-bottom:20px; font-size:18px; text-align:center; font-weight:700;"
        >
          {{
            translate(
              "Thank you for using Libyano Robot! Its been my pleasure to serve you!"
            )
          }}
        </v-card-text>
        <v-rating
          length="5"
          size="54"
          value="3"
          color="#92278f"
          background-color="#CE93D8"
          class="d-flex justify-center mb-6"
        ></v-rating>

        <v-divider></v-divider>

        <v-card-actions class="d-flex justify-center">
          <v-btn
            color="orange lighten-3"
            dark
            large
            width="160"
            @click="change_mode();rate=false"
            style="font-weight:700;"
          >
           {{translate("Done")}}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="voucher.dialog.show" max-width="400px" persistent>
      <v-card class="mx-auto" max-width="400" outlined dark color="#92278f">
        <v-card-title
          >{{ voucher.dialog.messages[voucher.dialog.counter].title }}
        </v-card-title>
        <v-card-text
          v-if="voucher.dialog.counter == 0"
          class="text-h5 font-weight-bold"
        >
          <p>
            {{ voucher.dialog.messages[0].text[0] }}
          </p>
        </v-card-text>

        <v-card-text class="text-h5 font-weight-bold" v-else>
          <p>
            {{ voucher.dialog.messages[0].text[0] }}
          </p>
          <p>
            {{ voucher.dialog.messages[0].text[0] }}
          </p>
          <p>{{ translate("Secret") }}: {{ voucher.card.secret }}</p>
          <p>Serial: {{ voucher.card.serial }}</p>
          <p>Val: {{ voucher.card.val }}</p>
        </v-card-text>
        <v-card-actions>
          <v-btn color="#ffc74a" @click="close_dialog()" light>
            {{ voucher.dialog.messages[voucher.dialog.counter].action }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <dir
      v-if="interactive"
      @click="goBack()"
      :class="rtl ? 'goback_right' : 'goback_left'"
    ></dir>

    <v-app-bar
      v-if="interactive"
      app
      flat
      extended
      extension-height="140px;"
      color="background"
      src="./assets/bar/banner2.svg"
    >
      <v-container>
        <v-row>
          <v-img
            @click="goBack"
            :src="translateMedia('./assets/bar/back.svg')"
            style="margin-top: 120px"
            max-width="60px"
            max-height="60px"
          ></v-img>
          <v-spacer></v-spacer>
          <v-spacer></v-spacer>
          <v-img
            src="./assets/bar/dima_m3ana.svg"
            max-width="260"
            max-height="100"
            style="margin-top: 140px;"
            class="ms-16"
          ></v-img>
          <v-spacer></v-spacer>
          <v-spacer></v-spacer>
          <v-img
            @click="set_lang('en')"
            src="./assets/bar/en.svg"
            style="margin-top: 120px"
            max-width="60px"
            max-height="60px"
          ></v-img>
          <v-img
            @click="set_lang('ar')"
            src="./assets/bar/ar.svg"
            style="margin-top: 120px;"
            max-width="60px"
            max-height="60px"
            class="ms-3"
          ></v-img>
        </v-row>
      </v-container>
    </v-app-bar>

    <v-main>
      <router-view></router-view>
    </v-main>

    <v-footer v-if="interactive_footer" app height="100" color="stream">
      <v-container>
        <v-row justify="space-between">
          <v-col md="3" style="margin-top:-2px;" @click="libyanoVidPlay()">
            <v-img
              src="./assets/bar/libyano.svg"
              max-height="300px"
              max-width="300px"
            ></v-img>
          </v-col>
          <v-col md="3" style="display: flex; margin-top:-2px;">
            <v-img
              src="./assets/bar/stream.svg"
              max-width="90px"
              max-height="70px"
              style="margin-left: 80px"
              @click="stream = true"
            ></v-img>
            <v-img
              src="./assets/bar/libyana.svg"
              max-width="70px"
              max-height="70px"
              style="margin-left: 40px;"
              @click="libyana = true"
            ></v-img>
          </v-col>
        </v-row>
        <v-dialog v-model="libyanoVid" width="720">
          <video id="libyanoVid" autoplay>
            <source src="./assets/vid/libyano.mp4" type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </v-dialog>
        <v-dialog v-model="libyana" max-width="750px">
          <v-card class="mx-auto" max-width="750" outlined>
            <v-card-text
              class="text--primary"
              style="padding-top:60px; margin-bottom:20px; height:220px; font-size:18px; text-align:center;"
            >
              {{
                translate(
                  "Libyana Brief Description"
                )
              }}
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-btn text @click="libyana = false">
                Close
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="stream" max-width="750px">
          <v-card class="mx-auto" max-width="750" outlined>
            <v-card-text
              class="text--primary"
              style="padding-top:60px; margin-bottom:20px; height:220px; font-size:18px; text-align:center;"
            >
              {{
                translate(
                  "Stream Brief Description"
                )
              }}
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-btn text @click="stream = false">
                Close
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-container>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  name: "App",

  methods: {
    rateWindow() {
      this.done = false
      this.rate = true
    },
    libyanoVidPlay() {
      this.libyanoVid = true;
    },
    reset_timeout() {
      console.log("resetting time out");
      clearTimeout(this.timeout_handler);
      this.timeout_handler = setTimeout(() => {
        if (this.$route.name != "slide-show") {
          this.change_mode()
          // this.$router.push({ name: "slide-show" });
        }
      }, 180e3);
    },
    change_mode() {
      this.$store
        .dispatch("Ros/change_mode", "grtcancel", { root: true })
        .then(() => {})
        .catch((err) => {
          console.error(
            "Error while trying to change mode from interface",
            err
          );
        });
    },
    set_lang(lang) {
      this.$store.dispatch("Interface/set_lang", lang, { route: true });
    },
    goBack() {
      var path = this.$route.path.split("/");
      path = path.slice(0, path.length - 1).join("/");
      if (!path) {
        // this.$router.push({ name: "slide-show" });
        this.done = true
      } else {
        this.$router.push(path);
      }
    },
    translateMedia(src) {
      return this.$store.getters["Interface/get_media"][src];
    },
    translate(phrase) {
      var t_phrase = this.$store.getters["Interface/get_content"][phrase];
      if (!t_phrase) {
        return phrase;
      } else {
        return t_phrase;
      }
    },
    close_dialog() {
      this.voucher.dialog.counter += 1;
      if (this.voucher.dialog.counter == 2) {
        clearTimeout(this.voucher.timeout);
        this.$store.dispatch("Voucher/clear", null, { root: true });
        this.voucher.continue = true;
        this.voucher.dialog.show = false;
        this.voucher.card = {};
      }
    },
  },

  computed: {
    loading() {
      return this.$store.getters["Loading/getLoading"];
    },
    interactive() {
      return this.$store.getters["Interactive/get_interactive"];
    },

    interactive_footer() {
      return this.$store.getters["Interactive/get_interactive_footer"];
    },

    voucher_show() {
      return this.$store.getters["Voucher/show"];
    },

    voucher_message() {
      return this.$store.getters["Voucher/message"];
    },
    lang() {
      return this.$store.getters["Interface/get_lang"];
    },
    rtl() {
      return this.$vuetify.rtl;
    },
  },
  watch: {
    libyanoVid() {
      var myVideo = document.getElementById("libyanoVid");
      if (this.libyanoVid == false) {
        myVideo.pause();
      } else {
        myVideo.load();
      }
    },
    voucher_show() {
      if (this.voucher_show) {
        this.voucher.card = this.voucher_message;
        this.voucher.dialog.counter = 0;
        this.voucher.dialog.show = true;
        this.voucher.continue = false;
        this.voucher.timeout = setTimeout(() => {
          this.$store.dispatch("Voucher/clear", null, { root: true });
          this.voucher.continue = true;
          this.voucher.dialog.show = false;
          this.voucher.card = {};
        }, 60e3);
      }
    },
    lang() {
      if (this.lang != this.current_lang) {
        this.current_lang = this.lang;
        if (this.current_lang == "ar") {
          this.appStyle.fontFamily = "Tajawal";
          this.$vuetify.rtl = true;
          console.log("Font and RTL Activated");
        } else {
          this.appStyle.fontFamily = "Montserrat";
          this.$vuetify.rtl = false;
        }
      }
    },
  },
  created() {
    var self = this
    setInterval(()=>{
      if(self.voucher.continue){
        self.$store.dispatch('Voucher/check',null,{root:true}).then(()=>{}).catch(()=>{})
      }
    },500)
    setInterval(()=>{
      self.$store.dispatch('Ros/fetch_mode',null,{root:true}).then((mode)=>{
          if(['mnl','str'].some(e=>e==mode)){
            if(this.$route.name != 'bootup'){
              this.$router.push({name:'bootup'})
            }
          }else if(['nav','chg','sth'].some(e=>e==mode)){
            if(this.$route.name != 'slide-show'){
              this.$router.push({name:'slide-show'})
            }
          }else if(mode == 'grt'){
            if(this.$route.name == 'slide-show' || this.$route.name == 'bootup'){
              this.$router.push({name:'language'})
            }
          }
      }).catch(()=>{})
    },250)
    this.reset_timeout();
    window.addEventListener("click", this.reset_timeout);
  },
  data() {
    return {
      done: false,
      rate: false,
      libyanoVid: false,
      libyana: false,
      stream: false,
      voucher: {
        dialog: {
          messages: [
            {
              title: "You Have Succeed",
              text: ["Please Press continue to get you voucher!"],
              action: "get",
            },
            {
              title: "Voucher Card",
              text: ["Please use the shown Voucher Card!", "", "", ""],
              action: "Received",
            },
            {},
          ],
          counter: 0,
          show: false,
          timeout: {},
        },
        card: {},
        continue: true,
      },
      appStyle: {
        backgroundColor: "#ecf0f1",
        fontFamily: "Montserrat",
      },
      timeout_handler: null,
      current_lang: "",
    };
  },
};
</script>
